@import "../../_settings.scss";

.Footer-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    width: 100%;
    padding-left: 1%;
    padding-right: 1%;
    background-color: #998f8f9a;
}

.Footer-NavLink-block-logo {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 4vh;
    width: 15%;
        @media screen and (max-width: 768px) {
                    width: 25%;
            }

}

.Footer-block-logo {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 4vh;
    width: 100%;
    // cursor: pointer;
    // border: $borderStyle;
    // border-radius: $borderRadius;
}

.Footer-logo-icon {
    width: 30px;
    height: 30px;
    border-radius: 10px;
    margin-right: 5px;

    @media screen and (max-width: 768px) {
        display: none;
    }
}

.Footer-logo-name {
    width: 100px;
    height: 30px;
    border-radius: 10px;
}

// .Nav-Maybar {
//     font-family: $font-2;
// }

.Footer-ul {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
    width: 70%;
    // font-family: $font-2;
}

.Footer-li {
    font-family: $font-2;
    cursor: pointer;
    // display: flex;
    // justify-content: center;
    // align-items: center;
    // height: 4vh;
    // border: $borderStyle; 
    // border-radius: $borderRadius;
    // background-color:$color-5;
    // cursor: pointer;
    // padding: 1%;
}

.Footer-li-contact {
    font-family: $font-2;
    cursor: pointer;

    @media screen and (max-width: 768px) {
        display: none;
    }
}


@media screen and (max-width: 768px) {

    .Footer-news,
    .Footer-menu,
    .Footer-presta,
    .Footer-li-contact,
    .Footer-condition {
        display: none;

    }
}

@media screen and (min-width: 768px) {

    .Footer-news-icon,
    .Footer-carte-icon,
    .Footer-presta-icon,
    .Footer-contact-icon,
    .Footer-condition-icon {
        display: none;
    }
}



.Footer-div-social-fb {
    width: 40px;
    height: 40px;
    border: $borderStyle;
    border-radius: $borderRadius;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #3b5998;
}

.Footer-div-social-instagram {
    width: 40px;
    height: 40px;
    border: $borderStyle;
    border-radius: $borderRadius;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $background-logo-instagram
}


.Footer-social-icon-fb {
    width: 70%;
    height: 70%;
    cursor: pointer;
}

.Footer-social-icon-insta {
    width: 80%;
    height: 80%;
    cursor: pointer;
}