@import "../../_settings.scss";

.DCV2-Cocktails {
    z-index: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    border: $borderStyle;
    border-radius: $borderRadius;
    color: $color-3;
}

.DCV2-Cocktails-texte {
    display: flex;
    flex-direction: column;
    width: 100%;
    // height: 40%;
    // border: $borderStyle;
    // border-radius: $borderRadius;
    color: $color-3;
}

.DCV2-Cocktails-title {
    display: flex;
    width: 100%;
    // height: 20%;
    padding: 1%;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-family: $font-2;
    font-style: italic;
    font-weight: bolder;
    font-size: large;
}

.DCV2-Cocktails-block-p {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: $borderStyle;
    border-radius: $borderRadius;
    // height: 80%;
    // max-height: 80%;
    width: 100%;
    white-space: pre-line;
    overflow: hidden;
    text-overflow: ellipsis;
}

.DCV2-Cocktails-block-p-span {
    display: flex;
    flex-direction: column;
    width: 100%;
    // height: 100%;
    line-height: 150%;
    text-align: center;
    white-space: pre-line;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 1%;
}

.DCV2-main-swiper-cocktails-card {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    // height: 100%;
    // width: 100%;
}

